.post__band--search-btn {
  height: 45px;
  width: auto;
  background: #000000;
  border-radius: 50px;
  color: #ffffff;
  border: none;
  padding: 0 15px;
}
.post__band--search-btn:hover {
  background: #333333;
  cursor: pointer;
}
.post__band--search-toggle {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 28px;
}
.post__band--search-toggle input[type=checkbox] {
  display: none;
}
.post__band--search-toggle label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  border-radius: 28px;
}
.post__band--search-toggle .toggle-text {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  font-weight: bold;
  transition: opacity 0.2s ease-in-out;
}
.post__band--search-toggle .toggle-text.toggle-text-fr {
  left: 0px;
}
.post__band--search-toggle .toggle-text.toggle-text-en {
  right: 10px;
}
.post__band--search-toggle input[type=checkbox]:checked + label .toggle-text-fr {
  opacity: 0.5;
}
.post__band--search-toggle input[type=checkbox]:checked + label .toggle-text-en {
  opacity: 1;
}
.post__band--search-toggle input[type=checkbox]:not(:checked) + label .toggle-text-en {
  opacity: 0.5;
}
.post__band--search-toggle input[type=checkbox]:not(:checked) + label .toggle-text-fr {
  opacity: 1;
}
.post__band--search .search-container {
  position: relative;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #c4c4c4;
}
.post__band--search .search-container__input {
  position: absolute;
  width: 0;
  height: 100%;
  padding: 10px;
  border: none;
  background-color: transparent;
  color: #333;
  transition: width 0.3s ease-in-out;
  font-size: 16px;
  text-align: center;
}
.post__band--search .search-container__input:focus {
  outline: none;
  width: 200px;
}
.post__band--search .search-container__icon {
  position: absolute;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.post__band--search .search-container__icon i {
  font-size: 1.25rem;
  color: #333;
