.conseil .collapse.number_1 .content {
  background-color: #1184D1;
}
.conseil .collapse.number_2 {
  background: #0777C2;
}
.conseil .collapse.number_3 {
  background: #EF8451;
}
.conseil .collapse.number_4 {
  background: #EA6419;
}
.conseil .collapse:target a:before {
  content: "\f061";
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  background: #ffffff;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  position: absolute;
  top: 25px;
  right: 30px;
  color: #000000;
  border-radius: 50%;
  transform: rotate(-45deg);
  transform-origin: 50% 50%;
}
.conseil .collapse:target a:before {
  animation-name: rotate-icon;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}
@keyframes rotate-icon {
  0% {
    transform: rotate(-45deg);
  }
  100% {
    transform: rotate(-90deg);
  }
}
.conseil .inner-content {
  padding: 1.8rem;
}
.conseil .inner-content-text {
  font-size: 1.6875rem;
  max-width: 300px;
}
.conseil .inner-content-text--small {
  font-size: 1rem;
  border-top: 1px solid #ffffff;
  max-width: 300px;
  padding-top: 15px;
}
.conseil .inner-content .number {
  font-size: 2.5rem;
  font-weight: 500;
}
.conseil .content {
  max-height: 0em;
  transition: max-height 0.3s ease-in-out;
  overflow: hidden;
  background-image: url("../images/cercle.svg");
  background-repeat: no-repeat;
  background-position: 80% 10%;
}
.conseil h3 {
  margin-bottom: 15px;
}
.conseil .collapse:target .content {
  max-height: 22em;
}
@media (max-width: 768px) {
  .conseil .collapse-content {
    width: 80vw;
    margin: auto;
  }
}
@media (max-width: 425px) {
  .conseil body {
    line-height: 1.3;
  }
  .conseil .collapse-content {
    width: 80vw;
  }
  .conseil .inner-content {
    padding: 1.2rem;
  }
  .conseil .inner-content h3 {
    margin-bottom: 0.3rem;
  }
}
@media (max-width: 320px) {
  .conseil body {
    line-height: 1.3;
  }
  .conseil .collapse-content {
    width: 80vw;
  }
  .conseil .inner-content {
    padding: 0.8rem;
  }
  .conseil .inner-content h3 {
    margin-bottom: 0.3rem;
  }
}
.conseil__title {
  display: flex;
  align-items: baseline;
  max-width: 200px;
}
.conseil__title .dash-container {
  text-align: center;
  margin-right: 15px;
}
.conseil__title .dash-container .small-dash {
  margin: 0 auto;
}
.conseil__title--container {
  margin-top: 116px;
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 991px) {
  .conseil__title--container {
    margin-top: 50px;
  }
}
.conseil__title--container a {
  display: flex;
  align-items: center;
  color: #237FBD;
}
.conseil__title--container a i {
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 1px solid #c4c4c4;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  transform: rotate(-45deg);
  margin-left: 15px;
  color: #000000;
  font-size: 1.25rem;
}